export default {
  name: 'NewsLetter',
  data() {
    return {
      email: '',
    }
  },
  methods: {
    async handleSuscribe(e) {
      e.preventDefault()
      this.$swal({
        title: 'Successful!',
        text: 'Thanks for Subscribing',
        icon: 'success',
        button: 'Ok',
      })
      await this.$strapi.$subscribers.create({ Email: this.email })
      // clear email input
      this.email = ''
    },
  },
}
